import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Form, message, Input, Button } from 'antd';
import { TableDataEditRow, CartHeadAction } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import { numberToMoney } from 'utils/formatUtil';
import { getEnrollReceivables, createEnrollReceivable } from 'services/admissionFee';
import SearchBar from './SearchBar';

class AdmissionFee extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  columns = [
    {
      title: 'Ngành đào tạo',
      dataIndex: 'studyField',
      key: 'studyField',
    },
    {
      title: 'Khoản thu nhập học',
      dataIndex: 'receivable',
      key: 'receivable',
    },
    {
      title: 'Số tiền',
      dataIndex: 'amount',
      key: 'amount',
      editable: true,
      render: text => numberToMoney(text),
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('amount', {
            initialValue: record.amount,
          })(<Input />)}
        </Form.Item>
      ),
    },
  ];

  handleChangeQueryParams = () => {
    if (getQueryParams().yearEduId && getQueryParams().trainTypeId) {
      this.setState({ paramSearch: getQueryParams() });
    }
  };

  handleCopyToNextYear = () => {
    message.success('Sao chép sang kì sau thành công');
  };

  handleSaveRowData = async rowData => {
    try {
      await createEnrollReceivable(rowData);
      return rowData;
    } catch (e) {
      return null;
    }
  };

  render() {
    return (
      <Card>
        <CartHeadAction>
          <Button type="primary" onClick={this.handleCopyToNextYear}>
            Sao chép sang kì sau
          </Button>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <SearchBar />
          </Col>
          <Col span={24}>
            <TableDataEditRow
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getEnrollReceivables}
              columns={this.columns}
              handleSaveRowData={this.handleSaveRowData}
              allowGetData={
                !!this.state.paramSearch.yearEduId && !!this.state.paramSearch.trainTypeId
              }
              rowKey="key"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AdmissionFee />
  </Page>
);



