import { request } from 'utils';

const prefix = '/api/finance';

export const getEnrollReceivables = async ({
  yearEduId,
  trainTypeId,
  studyFieldId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) => {
  const result = await request({
    prefix,
    url: '/EnrollReceivable',
    method:'GET',
    params: { yearEduId, trainTypeId, studyFieldId, page, pageSize, orderBy, isPagingEnabled },
    data: {}
  });
  if(result.data && result.data.items) {
    result.data.items.forEach(item => {
      item.key = `${item.yearEduId}_${item.trainTypeId}_${item.studyFieldId}_${item.receivableId}`;
    });
  }
  return result;
}

  
export const createEnrollReceivable = ({ id, yearEduId, trainTypeId, studyFieldId, receivableId, amount } = {}) =>
  request({
    prefix,
    url: '/EnrollReceivable',
    method: 'POST',
    params: {},
    data: { id, yearEduId, trainTypeId, studyFieldId, receivableId, amount }
  });

export const getEnrollReceivable = id =>
  request({
    prefix,
    url: `/BonusNote/${id}`,
    method: 'GET',
  });


