import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { getYearEducations } from 'services/yearEducationService';
import { getTrainTypes } from 'services/trainType';
import { getStudyFields } from 'services/StudyField';
import { SelectAjax } from 'components/common';

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  formItemLayout = 6;

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    const {
      form: { getFieldDecorator, setFieldsValue, getFieldValue },
    } = this.props;
    const trainTypeId = getFieldValue('trainTypeId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col xs={24} sm={24} md={24} lg={8} xl={6}>
            <Form.Item label="Năm học">
              {getFieldDecorator('yearEduId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} sm={24} md={24} lg={8} xl={6}>
            <Form.Item label="Hệ">
              {getFieldDecorator('trainTypeId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col xs={24} sm={24} md={24} lg={8} xl={6}>
            <Form.Item label="CN">
              {getFieldDecorator('studyFieldId')(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId }}
                  service={getStudyFields}
                  allowGetData={!!trainTypeId}
                />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} sm={24} md={24} lg={24} xl={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



